<template>
    <div>
        <div class="swiper">
            <van-swipe :autoplay="3000">
                <van-swipe-item v-for="(image, index) in product.images" :key="index">
                    <img v-lazy="image.src" />
                </van-swipe-item>
            </van-swipe>
        </div>

        <div class="sku-box">
            <van-sku v-model="showBase"
                :sku="sku"
                :goods="product"
                :goods-id="productId"
                @buy-clicked="onBuyClicked"
                @add-cart="onAddCartClicked">
                <template slot="sku-header-price" slot-scope="props">
                    <div class="van-sku__goods-price">
                    <span class="van-sku__price-symbol">AU$ </span><span class="van-sku__price-num">{{ props.price }}</span>
                    </div>
                </template>
                <!--  sku actions -->
                <template slot="sku-actions" slot-scope="props">
                    <div class="van-sku-actions">
                    <van-button bottom-action @click="onPointClicked"></van-button>
                    <!--  sku  onBuyClicked  -->
                    <van-button type="primary" bottom-action @click="props.skuEventBus.$emit('sku:buy')"></van-button>
                    </div>
                </template>
            </van-sku>
        </div>

        <van-goods-action>
            <van-goods-action-mini-btn icon="chat" text="" @click="onClickService" />
            <van-goods-action-mini-btn icon="cart" text="" @click="onClickShoppingCart" />
            <van-goods-action-big-btn text="" @click="onClickAddToShoppingCart" />
            <van-goods-action-big-btn text="" @click="onClickPurchase" primary />
        </van-goods-action>
    </div>
</template>

<script>
import { Icon, Swipe, SwipeItem, Row, Col, Sku, Button,
GoodsAction, GoodsActionBigBtn, GoodsActionMiniBtn } from 'vant';
export default {
    components: {
        [Icon.name]: Icon,
        [Swipe.name]: Swipe,
        [SwipeItem.name]: SwipeItem,
        [Row.name]: Row,
        [Button.name]: Button,
        [Col.name]: Col,
        [Sku.name]: Sku,
        [GoodsAction.name]: GoodsAction,
        [GoodsActionBigBtn.name]: GoodsActionBigBtn,
        [GoodsActionMiniBtn.name]: GoodsActionMiniBtn,
    },
    data() {
        return {
            showBase: false,
            productId: 0,
            active: 1,
            product: {},
            goods: {},
            sku: {
                tree: [
                    {
                        k: '', // skuKeyName
                        v: [
                            {
                                id: '30349', // skuValueId id
                                name: '', // skuValueName
                                imgUrl: 'https://img.yzcdn.cn/1.jpg' // 
                            },
                            {
                                id: '1215',
                                name: '',
                                imgUrl: 'https://img.yzcdn.cn/2.jpg'
                            }
                        ],
                        k_s: 's1' // skuKeyStrsku  list key value  id
                    }
                ],
                list: [
                    {
                        id: 2259, // skuId
                        price: 100, // 
                        s1: '1215', //  k_s  s1  id
                        s2: '1193', //  k_s  s2  id
                        s3: '0', // 30
                        stock_num: 110 //  sku 
                    }
                ],
                price: '1.00', // 
                stock_num: 227, // 
                collection_id: 2261, //  skuId  collection_id sku  id
                none_sku: false, // 
                messages: [],
                hide_stock: false // 
            }
        };
    },
    methods: {
        loadProducts(id) {
            this.$axios.get('/api/products/' + id).then((res) => {
                this.product = res.data;
                this.sku.none_sku = this.product.skus.length > 0;
                this.sku.price = (this.product.amount / 100).toFixed(2);
                this.sku.stock_num = this.product.stock;
                this.goods = {
                    title: this.product.title,
                    picture: this.product.cover,
                };
                this.sku.tree = this.product.skus.map((element) => {
                    return {
                        k: element.title,
                        v: [],
                        k_s: '',
                    };
                });
                this.sku.list = this.product.skus.map((element) => {
                    return {

                    };
                });
            });
        },
        onClickService() {

        },
        onClickShoppingCart() {
            console.log(this.showBase);
            if (this.product.skus.length) {
                //
            } else {
                //
            }
        },
        onClickAddToShoppingCart() {
            this.showBase = true;
        },
        onClickPurchase() {

        },
        onBuyClicked() {

        },
        onAddCartClicked() {

        },
    },
    created() {
        const params = this.$route.params;
        this.productId = params.id;
    },
    mounted() {
        this.$nextTick((res) => {
            if (this.productId > 0) {
                this.loadProducts(this.productId);
            }
        });
    },
};
</script>

<style lang="less">
.swiper {
    img {
        height: 240px;
    }
}
</style>



